.track-weather { color: #15171a; font-family: sans-serif}

.display-flex { display: flex}

.track-weather .label { color: #738a94 }

.track-weather .text { color: #15171a }

.track-weather .row {
  flex-direction: row;
  justify-content: space-evenly
}

.track-weather .col-2, .track-weather .col-3 {
  flex-direction: column;
  border-left: 0.1rem solid #fe4a00;
  margin-left: 2rem;
  padding-left: 2rem
}

.track-weather .temp { font: 9.5rem/9.5rem; margin: 0}

@media (max-width: 767.98px) {
  .track-weather .col-2, 
  .track-weather .col-3 {
    padding-left: 1rem;
    margin-left: 1rem
  }
}

@media (max-width: 575.98px) {
  .track-weather .row {
    flex-direction: column;
    justify-content: flex-start
  }
  
  .track-weather .temp {
    font-size: 9rem;
    text-align: center
  }

  .track-weather .col-2, .track-weather .col-3 {
    border-top: 0.1rem solid #fe4a00;
    border-left: none;
    margin-right: 2rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem
  }
}